export default {
  props: {
    score: {
      type: Number,
      default: 0
    },
    index: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {}
  },
  computed: {
    width () {
      const splitPoint = 2.5
      const max = 5
      const minWidth = 25
      if (this.score <= splitPoint) {
        return minWidth
      } else {
        const v = (this.score - splitPoint) / (max - splitPoint) * 100
        return v > minWidth ? v : minWidth
      }
    }
  },
  mounted () {
  }
}
